html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-rubik);
  font-size: 18px;
  background: var(--veryDarkBlue);
  color: var(--paleBlue);
}

:root {
  /* Primary */
  --blue: hsl(246, 80%, 60%);
  --lightRed-work: hsl(15, 100%, 70%);
  --softBlue-play: hsl(195, 74%, 62%);
  --lightRed-study: hsl(348, 100%, 68%);
  --limeGreen-exercise: hsl(145, 58%, 55%);
  --violet-social: hsl(264, 64%, 52%);
  --softOrange-selfCare: hsl(43, 84%, 65%);
  /* Neutral */
  --veryDarkBlue: hsl(226, 43%, 10%);
  --darkBlue: hsl(235, 46%, 20%);
  --desaturatedBlue: hsl(235, 45%, 61%);
  --desaturatedBlue-hover: hsl(235, 45%, 35%);
  --paleBlue: hsl(236, 100%, 87%);
  --paleBlue-title:hsl(236, 100%, 100%);
}

:root {
  --font-rubik: "Rubik", sans-serif;
}

.card {
  margin: 15px;
}
@media only screen and (min-width: 1140px) {
  .card {
    display: grid;
    grid-template-areas: "one two tre for" "one five six seve";
    column-gap: 30px;
    justify-content: center;
    align-items: center;
    margin: 30px 0 0 0;
  }
}
.card__person {
  background: var(--darkBlue);
  border-radius: 15px;
  width: 100%;
  height: 100%;
  position: relative;
  margin-bottom: 80px;
}
@media only screen and (min-width: 1140px) {
  .card__person {
    grid-area: one;
    width: 90%;
    margin: 0 auto;
  }
}
.card__person--top {
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--blue);
  border-radius: 15px;
}
@media only screen and (min-width: 1140px) {
  .card__person--top {
    padding: 15px;
    justify-content: right;
    flex-direction: column;
    height: 70%;
  }
}
.card__person--top img {
  border: 3px solid var(--paleBlue-title);
  border-radius: 50%;
  max-width: 100%;
  height: auto;
  margin: 15px;
}
@media only screen and (min-width: 1140px) {
  .card__person--top img {
    align-self: start;
    margin-bottom: 40px;
  }
}
.card__person--top span {
  text-align: center;
  font-weight: 400;
}
@media only screen and (min-width: 1140px) {
  .card__person--top span {
    text-align: left;
    padding: 15px;
  }
}
.card__person--top h1 {
  font-size: 20px;
  font-weight: 300;
  color: var(--paleBlue-title);
}
@media only screen and (min-width: 1140px) {
  .card__person--top h1 {
    text-align: left;
    font-size: 35px;
    padding: 0 0 0 15px;
    margin-bottom: 20px;
    width: 60%;
  }
}
.card__person--bottom {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-content: center;
}
@media only screen and (min-width: 1140px) {
  .card__person--bottom {
    flex-direction: column;
    height: 30%;
    padding: 15px;
  }
}
.card__person--bottom .active {
  color: var(--paleBlue-title);
}
.card__person--bottom a {
  text-decoration: none;
  color: var(--desaturatedBlue);
}
.card__person--bottom a:hover {
  color: var(--paleBlue-title);
}
.card__work {
  margin-bottom: 80px;
}
@media only screen and (min-width: 1140px) {
  .card__work {
    grid-area: two;
  }
}
.card__play {
  margin-bottom: 80px;
}
@media only screen and (min-width: 1140px) {
  .card__play {
    grid-area: tre;
  }
}
.card__study {
  margin-bottom: 80px;
}
@media only screen and (min-width: 1140px) {
  .card__study {
    grid-area: for;
  }
}
.card__exercise {
  margin-bottom: 80px;
}
@media only screen and (min-width: 1140px) {
  .card__exercise {
    grid-area: five;
    margin-bottom: 0 !important;
  }
}
.card__social {
  margin-bottom: 80px;
}
@media only screen and (min-width: 1140px) {
  .card__social {
    grid-area: six;
    margin-bottom: 0 !important;
  }
}
@media only screen and (min-width: 1140px) {
  .card__selfcare {
    grid-area: seve;
    margin-bottom: 0 !important;
  }
}

@media only screen and (min-width: 1140px) {
  footer {
    margin-top: 20px;
  }
}

.grid {
  position: relative;
}
@media only screen and (min-width: 1140px) {
  .grid {
    margin-bottom: 30px;
    margin-top: 50px;
  }
}
.grid__image {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  text-align: right;
  padding-right: 15px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: -48px;
}
.grid__image--work {
  background: var(--lightRed-work);
}
.grid__image--play {
  background: var(--softBlue-play);
}
.grid__image--study {
  background: var(--lightRed-study);
}
.grid__image--exercise {
  background: var(--limeGreen-exercise);
}
.grid__image--social {
  background: var(--violet-social);
}
.grid__image--selfcare {
  background: var(--softOrange-selfCare);
}
.grid__info {
  position: inherit;
  background: var(--darkBlue);
  border-radius: 15px;
  display: grid;
  grid-template-areas: "bac bac bac bac" "work one one img" "hours one one week";
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  line-height: 1.6;
  cursor: pointer;
}
.grid__info:hover {
  background: var(--desaturatedBlue-hover);
  transition: 0.3s ease;
}
@media only screen and (min-width: 1140px) {
  .grid__info {
    grid-template-areas: "work one one img" "hours hours two two" "time time two two";
    align-items: center;
    justify-content: center;
  }
}
.grid__info .title {
  grid-area: work;
  padding-left: 9px;
  margin-top: 9px;
  font-size: 18px;
  font-weight: 400;
  color: var(--paleBlue-title);
}
@media only screen and (min-width: 1140px) {
  .grid__info .title {
    grid-area: work;
    text-align: left;
    margin-bottom: 15px;
  }
}
.grid__info .time {
  grid-area: hours;
  padding-left: 9px;
  font-size: 30px;
  font-weight: 300;
  text-align: left;
  color: var(--paleBlue-title);
}
@media only screen and (min-width: 1140px) {
  .grid__info .time {
    grid-area: hours;
  }
}
.grid__info .image {
  grid-area: img;
  text-align: right;
  padding-right: 9px;
}
@media only screen and (min-width: 1140px) {
  .grid__info .image {
    grid-area: img;
    margin-bottom: 12px;
    margin-left: 40px;
  }
}
.grid__info .image button {
  background: none;
  border: none;
  cursor: pointer;
}
.grid__info .prev {
  grid-area: week;
  text-align: right;
  padding-right: 10px;
}
@media only screen and (min-width: 1140px) {
  .grid__info .prev {
    grid-area: time;
    padding-left: 10px;
    margin-bottom: 10px;
  }
}